.flip-card {
    background-color: transparent;
    height: 360px;
    width: 16%;
    border: none;
    /* perspective: 1000px; */
    margin: 2px;
    font-size: 20px;
    cursor: pointer;
}

.flip-card img {
    width: 100%;
}

.flip {
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}



.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    /* transition: transform 0.5s; */
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}


.flip-card-front img {
    border-radius: 20px;
}


.flip-card-back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background-color:  rgb(156, 222, 248);
    border: 5px solid rgb(130, 126, 79);
    border-radius: 20px;
    border-collapse: separate;
}

.grid {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    opacity: 1.0;
    padding: 30px;
    background-color: rgb(216, 210, 40);
    border-radius: 20px;
}


